import * as React from "react"
import { Link } from "gatsby"

const Home = ({ data, location, language }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`;
  const [isTextShown, setIsTextShown] = React.useState(false);
  const switchText = React.useCallback(() => { setIsTextShown(d => !d); }, []);
  const setTextOn = React.useCallback(() => { setIsTextShown(true); }, []);
  React.useEffect(() => {
    window.setTimeout(() => {
      setIsTextShown(true);
    }, 5000);
  }, [])

  return (
    <main id="homepage" className={isTextShown ? 'text-shown' : ''}>
      <img id="home-logo" src="/img/logo.svg" width={430}
        height={320}
        onMouseDown={setTextOn}
        onDragEnd={setTextOn}
        onPointerDown={setTextOn}
        onPointerUp={setTextOn}
        onTouchStart={setTextOn}
        onTouchEnd={setTextOn}
        alt={siteTitle}
      />
      <section id="home-text">
        {language === "en"
          ? <p><b>ALMOTÁSIM</b> is a studio focused on software, art and digital communication.</p>
          : <p><b>ALMOTÁSIM</b> es un estudio dedicado al software, el arte y la comunicación digital.</p>
        }
        {language === "en" ?
          <ul>
            <li><Link to={`/${language}/projects/`}>Projects</Link></li>
            <li><Link to={`/${language}/sketches/`}>Sketches</Link></li>
            {/* <li><Link to={`/${language}/texts/`}>Texts</Link></li> */}
            <li><Link to={`/${language}/about/`}>About</Link></li>
            {language === "en"
              ? <li><Link to="es/">Español</Link></li>
              : <li><Link to="/">English</Link></li>
            }
          </ul> : <ul>
            <li><Link to={`/${language}/projects/`}>Proyectos</Link></li>
            <li><Link to={`/${language}/sketches/`}>Bosquejos</Link></li>
            {/* <li><Link to={`/${language}/texts/`}>Texts</Link></li> */}
            <li><Link to={`/${language}/about/`}>Info</Link></li>
            {language === "en"
              ? <li><Link to="es/">Español</Link></li>
              : <li><Link to="/">English</Link></li>
            }
          </ul>}
      </section>
    </main>
  )
}

export default Home;
